import * as energy from "./energy"

const CACHE = "ENERGY_BY_ID/CACHE"

export const actions = {
  cacheEnergy: ({ id, data }) => ({ type: CACHE, id, data }),
}

const INITIAL_ENERGY_STATE = energy.reducer(undefined, {})

export function reducer(state = {}, action) {
  switch (action.type) {
    case CACHE: {
      const { id, data } = action
      return {
        ...state,
        [id]: energy.reducer(state[id], energy.actions.cacheEnergy(data)),
      }
    }
    default:
      return state
  }
}

export const selectors = {
  get: (state, id) => state[id] || INITIAL_ENERGY_STATE,
}
