const CACHE = "ENERGY/CACHE"

export const actions = {
  cacheEnergy: data => ({ type: CACHE, data }),
}

const INITIAL_STATE = []

function sortByTimestamp(a, b) {
  return a.unixTime - b.unixTime
}

export function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case CACHE: {
      const { data } = action

      return data.map((d, i) =>
        [...(state[i] || []), ...d].sort(sortByTimestamp)
      )
    }
    default:
      return state
  }
}
