import { combineReducers } from "redux"
import * as divSize from "./divSize"
import * as fetch from "./fetch"
import * as energyById from "./energyById"

export const reducer = combineReducers({
  divSize: divSize.reducer,
  fetch: fetch.reducer,
  energyById: energyById.reducer,
})

export const actions = {
  divSize: divSize.actions,
  fetch: fetch.actions,
  energyById: energyById.actions,
}

export const selectors = {
  fetch: fetch.selectors,
  energyById: energyById.selectors,
}
